* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
body {
  background-color: aliceblue;
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 20px auto;
  border: 1px solid #ccc;
  padding: 50px 30px 100px;
  background: url('../public/img/london01.jpg') center center / cover no-repeat;
  color: #fff;
}
.list h2 {
  text-align: center;
  margin-bottom: 30px;
}
.list form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}
.list form input {
  width: 300px;
  height: 40px;
  border: 1px solid #999;
  padding-left: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}
.list form button {
  width: 60px;
  height: 40px;
  border: 1px solid #fff;
  background-color: tomato;
  color: #fff;
  cursor: pointer;
}

/* 
nth-child(2n) => 2배수
nth-child(odd) = > 홀수
nth-child(even) => 짝수
 */
ul li {
  background-color: rgba(255, 255, 255, 0.6);
  color: #333;
  padding: 10px;
}
ul li:nth-child(odd):before {
  content: '🚗 ';
}
ul li:nth-child(even):before {
  content: '🚕 ';
}
ul li:last-child:before {
  content: '🚙 ';
}
ul li:first-child:before {
  content: '🚓 ';
}